@import url('https://fonts.googleapis.com/css2?family=Neonderthaw&display=swap');


.App {
  font-family: 'Neonderthaw', cursive;
  text-align: center;
  color: rgb(37, 216, 248);
  animation-name: headerani;
  animation-duration: 25s;
  animation-iteration-count: infinite;
  font-size: 42px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  color: #ffffff;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.text-box {
  text-align: center;
  color: #ffffff
}

.header {

  animation-name: 'headerani';
  animation-duration: 20s;
  animation-iteration-count: infinite;
}

.topNav {
  overflow: hidden;
  background: gray;
}

.navButton {
  float: left;
  display: block;
  color: #ffffff;
}

@media screen and (max-width: 600px) {
  .topNav {
    .navButton:not(:first-child) {display: none;}
  }
}

@keyframes headerani {
  0% {color: rgb(37, 216, 248); text-shadow: 0 0 15px rgb(37, 216, 248);}
  66% {color: rgb(37, 216, 248); text-shadow: 0 0 15px rgb(37, 216, 248);}
  67% {color: rgba(0, 0, 0); text-shadow: 0 0 10px rgba(0, 0, 0, 0);}
  68% {color: rgb(37, 216, 248); text-shadow: 0 0 15px rgb(37, 216, 248);}
  71% {color: rgb(37, 216, 248); text-shadow: 0 0 15px rgb(37, 216, 248);}
  72% {color: rgba(0, 0, 0,); text-shadow: 0 0 15px rgba(0, 0, 0, 0);}
  73% {color: rgb(37, 216, 248); text-shadow: 0 0 15px rgb(37, 216, 248);}
  74% {color: rgba(0, 0, 0,); text-shadow: 0 0 15px rgba(0, 0, 0, 0);}
  80% {color: rgba(0, 0, 0,); text-shadow: 0 0 15px rgba(0, 0, 0, 0);}
  81% {color: rgba(37, 216, 248, 0.5); text-shadow: 0 0 10px rgba(37, 216, 248, 0.5);}
  98% {color: rgba(37, 216, 248, 0.5); text-shadow: 0 0 10px rgba(37, 216, 248, 0.5);}
  99% {color: rgba(37, 216, 248); text-shadow: 0 0 15px rgb(37, 216, 248);}
  100% {color:rgb(37, 216, 248); text-shadow: 0 0 15px rgb(37, 216, 248);}
}